// large styles for the section "AZ ISTENTISZTELET"
.large {
  width: 50%;
  margin: 1rem 0 0 0;
  
  & .section_box  {
    & img {
    width: 25rem;
    border: 10px solid whitesmoke;
    }
    
  & .box_container {
      width: 20rem;
      background-color: #6c757d;
      float: left;
      position: relative;
      left: 8rem;
      top: -7rem;
      border: 2px solid whitesmoke;
      height: 7rem;


    & .o_box {
        width: 15rem;
        background-color: orange;
        float: left;
        position: absolute;
        top: -1rem;
        left: 1rem;

        & .box_text {
          margin: 1px auto;
          text-align: center;
          font-size: 1.8rem;
        }
    }

    & .o_title {
        color: whitesmoke;
        font-size: 1.5rem;
        line-height: 0.5rem;

        & .o1_text {
          margin: 2.5rem 0 0.2rem 0.9rem;
        }
        
        & .o2_text {
          margin: 1.5rem 0 1rem 1.7rem;
          padding-right: 0.3rem;
        }
    }
    
    }
  }
}


