.header-container {
    width: 100vw;
    background-color: transparent;
    position: relative;
  
  & .navbar {
    position: absolute;
    z-index: 10;
  }

  & .nav-left {
    display: flex;
    flex-direction: row;
    justify-content: left;  }

  & .nav-right {
    display: flex;
    flex-direction: row;
    margin-left: 55rem;
    margin-top:-5rem;
  }

  & .option {
    text-decoration: none;
    color:whitesmoke;
    position: relative;
    letter-spacing: 0.30rem;
    font-size: 17px;
    text-transform: uppercase;
    font-weight: 500;
  }
  
  & .option:after {    
    background: none repeat scroll 0 0 transparent;
    content: "";
    display: block;
    height: 2px;
    left: 50%;
    position: absolute;
    background: #fa990e;
    transition: width 0.2s ease 0s, left 0.2s ease 0s;
    width: 0;
  }
  & .option:hover:after { 
    width: 95%; 
    left: 0; 
  }
  
}

#logo-img {
  width: 2.5rem;
  border: 3px solid whitesmoke;
  border-radius: 45%;
}


.logo-text {
  font-size: 20px;
  line-height: 0.25;
  color: whitesmoke;
  padding-top: 1.8rem;
}

.caption {
  background-color: rgba(0,0,0,.6);  
  width: 30rem;
  color: white;
  margin: 0 0 22rem 45rem;
  border-radius: 0.5rem;
}

.carousel-indicators li {
  width: 10px;
  height: 10px;
  border-radius: 100%;
  margin-top: -8.5rem;
}
