
.pic_container {
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;
  padding-left: 12rem;
  height: 24rem;
}

#user_input {
        width: 40%;
        border: 1px solid #ccc;
        border-radius: 4px;
        background-color: #6c757d;
        margin-left: 0.2rem;
}

#submit-button {
    height: 1.8rem;
    background-color: #6c757d;
    color: white;
    cursor: pointer;
    border-radius: 5%;
    font-size: large;    

  &:hover {
    color: #fa990e;
    border: 0.5px solid whitesmoke;
    }
}

.address-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: white;
}
.title {
  font-family: 'PT Serif', serif;
  letter-spacing: 0.15rem;
  margin: -1rem;
}

.a-title {
  text-align: center;
  margin-bottom: 0.9rem;
  letter-spacing: 0.15rem;
}

h2 {
  margin-top: 0;
  text-align: center;
}

.address {
  display: flex;
  flex-direction: row;
  text-align: center;
  justify-content: center;
  padding: 1rem 0 1rem 0;
}

.address div {
  padding-right: 2rem;
  margin-top: 3.8rem;
}
.address img {
  width: 30%;
}

#megkozelites {
  text-align: center;
}

#megkozelites h3 {
    font-size: 1.5rem;
  }


.card-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 0 6rem 3.5rem 6rem;
  margin-top: -8rem;
  position: relative;
}
