.verse-wrapper {
    min-height: 14rem;
}

.verses {
    display: flex;
    flex-direction: column;
    padding: 1rem 0 0 5rem;
}

div#reading {
    border: 2px solid orange;
    border-radius: 0.5rem;
    width: 32.5rem;
    margin-left: 8rem;
    text-align: center;
    padding-top: 0.5rem;
    background-color: whitesmoke;
}

.follow {
    display: flex;
    flex-direction: row;
    padding-top: 0.8rem;
    margin-left: 0.2rem;

    & h5 {
        color:orangered;
    }
}

button {
    margin-left: 0.3rem;
    border-radius: 2rem;
    margin-top: -0.5rem;
}