.social-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  background-color: white;
  padding: 0 3rem;
  width: 100%;
}
  a.social {
    padding: 0 1rem;
    transition: transform 250ms;
    display: inline-block;
  }

  a.social:hover {
    transform: translateY(-2px);
  }
  a.youtube {
    color: #eb3223;
  }
  
  a.facebook {
    color: #4968ad;
  }
  
  a.twitter {
    color: #49a1eb;
  }
  
  a.instagram {
    color: black;
  }

  .display-label {
    display: inline;
    padding-right: 0.7rem;
  }

  form {
    padding: 0.5rem;
  }

  #submit-button {
    height: 2rem;
  }